<template>
    <div class="recommend">
        <div class="recommend-item" v-for="item in recommends.slice(0,4)" :key="item.id">
            <a href="" @click.prevent="goDt(item.id)">
                <img v-lazy="item.cover_url" alt="">
            </a>
            <div>{{item.title}}</div>
        </div>
    </div>
</template>

<script>
import {useRouter} from 'vue-router';

export default {
    name:'RecommendView',
    props: {
        recommends: {
            type:Array,
            defaule(){
                return [];
            }
        }
    },
    setup() {
        const router = useRouter();

        const goDt = (id) =>{
            router.push({path:'/detail', query:{id}})
        }

        return {
            goDt
        }
    }
}
</script>

<style scoped lang="scss">
.recommend {
    display: flex;
    width: 100%;
    text-align: center;
    padding: 15px 0 30px;
    font-size: 14px;
    background: #fff;
}
.recommend-item {
    flex: 1;
    img {
        width: 70px;
        height: 70px;
        margin-bottom: 10px;
    }
}
</style>