<template>
  <div class="tab-control">
    <div v-for="(item,index) in titles" :key="index"
      @click="itemClick(index)"
      class="tab-control-item" :class="{active:index == currentIndex}"> 
      <span>{{item}}</span>
    </div>
  </div>
</template>

<script>
import {ref} from 'vue'

export default {
    name:'TabControl',
    props: {
      titles: {
        type:Array,
        default(){
          return [];
        }
      }
    },
    setup(props, {emit} ){
      let currentIndex = ref(0);

      const itemClick = (index) => {
        currentIndex.value = index;
        emit('tabClick',index)
      }

      return {
        currentIndex,
        itemClick
      }
    }
}
</script>

<style scoped lang="scss"> 
  .tab-control {
    display: flex;
    height: 40px;
    line-height: 40px;
    font-size: 14px;
    text-align: center;
    background: #fff;
    width: 100%;
    margin-top: 5px;

    //CSS3
    position: sticky;
    top: 44px;
    z-index: 9;

    .tab-control-item {
      flex: 1;
      
      span {
        padding: 5.5px;
      }
    }
    .active {
      color: var(--color-high-tint);
      span {
        border-bottom: 3px solid var(--color-high-tint);
      }
    }
  }
</style>
